<!-- eslint-disable -->

<template>
  <form @submit.prevent="onSubmit">
    <div class="MyPage">
      <div class="container fluid">
        <div class="row align-tems-center">
          <div class="col">
            <h2 style="text-align:center;margin-top:20%"></h2>
            <h6 style="font-size: x-small; text-align:center;}">  {{Base.recent_update.substring(0, 10) }}</h6>
          </div>
          <div class="col">
            <div class="row" style="margin-top: 10%;">
              <h6 class="mt-3 col-sm-6">
                <span class="text-danger">*</span>
              </h6>
              <div class="mt-3 col-sm-6">{{ Base.user_name }}</div>
            </div>
            <v-divider></v-divider>
            <div class="row">
              <h6 class="mt-4 col-sm-6" style="margin-bottom: 0px;">
                ()<span class="text-danger">*</span>
              </h6>
              <div class="mt-4 col-sm-6">{{ Base.email }}</div>
            </div>
            <v-divider></v-divider>
            <div class="row">
              <h6 class="mt-4 col-sm-6" style="margin-bottom: 0px;">
                ()<span class="text-danger">*</span>
              </h6>
              <div class="mt-4 col-sm-6">{{ Base.surname }}</div>
            </div>
            <v-divider></v-divider>
            <div class="row">
              <h6 class="mt-4 col-sm-6" style="margin-bottom: 0px;">
                ()<span class="text-danger">*</span>
              </h6>
              <div class="mt-4 col-sm-6">{{ Base.name }}</div>
            </div>
            <v-divider></v-divider>
            <div class="row">
              <h6 class="mt-4 col-sm-6" style="margin-bottom: 0px;">
                <span class="text-danger">*</span>
              </h6>
              <div class="mt-4 col-sm-6">{{ Base.birthday }}</div>
            </div>
            <v-divider></v-divider>
            <h6 class="mt-3"><span class="text-danger">*</span></h6>
            <input
              type="text"
              class="form-control mt-2"
              id="pass_num"
              placeholder=""
              v-model="Base.pass_num"
              maxlength="9"
            />
            <v-divider></v-divider>
            <h6 class="mt-3"><span class="text-danger">*</span></h6>
            <input
              type="text"
              class="form-control mt-2"
              id="PHONE"
              placeholder="(-   )"
              v-model="Base.phone"
              maxlength="11"
            />
            <v-divider></v-divider>
            <h6 class="mt-3"><span class="text-danger">*</span></h6>
            <input
              type="text"
              class="form-control mt-2"
              id="POST"
              placeholder=""
              v-model="Base.post"
              maxlength="5"
            />
            <input
              type="text"
              class="form-control mt-2"
              id="ADDR1"
              placeholder=""
              v-model="Base.addr1"
              maxlength="40"
            />
            <input
              type="text"
              class="form-control mt-2"
              id="ADDR2"
              placeholder=""
              v-model="Base.addr2"
              maxlength="40"
            />
            <input
              type="button"
              @click="this.DaumPost"
              class="btn btn-primary btn-default btn-sm mt-2"
              value=" "
            />
            <v-divider></v-divider>
            <div class="extra row">
              <a href="/deleteuser" class="extra2"></a>
              <a href="/changepassword"> </a>
            </div>
            <button type="submit" class="btn btn-info form-control">
               
            </button>
          </div>
          <div class="col"></div>
        </div>
      </div>
    </div>
  </form>
</template>

<script>
/* eslint-disable */

export default {
  data() {
    return {
      Base: []
    };
  },
  mounted() {
    this.mydata();
  },
  methods: {
    mydata() {
      const params = new URLSearchParams();
      params.append("email", this.$session.get("email"));

      this.$axios.post("/user/userinfo", params).then(res => {
        this.Base = res.data;
      });
    },
    DaumPost() {
      new window.daum.Postcode({
        oncomplete: data => {
          if (data.jibunAddress == "") {
            document.getElementById("ADDR1").value = data.roadAddress;
          } else {
            document.getElementById("ADDR1").value = data.jibunAddress;
          }
          document.getElementById("POST").value = data.zonecode;
          document.getElementById("ADDR2").focus();
        }
      }).open();
    },
    onSubmit() {
      var pass_num = document.getElementById("pass_num").value;
      var PHONE = document.getElementById("PHONE").value;
      var POST = document.getElementById("POST").value;
      var ADDR1 = document.getElementById("ADDR1").value;
      var ADDR2 = document.getElementById("ADDR2").value;

      var passnumtest = /^(.*)[a-zA-Z]{1}[0-9a-zA-Z]{1}[0-9]{7}(.*)$/;
      var postTest = /^[0-9]{5}$/g;
      var phoneTest = /^01[0-9]{9}$/g;

      if (pass_num == "") {
        alert("  ");
        document.getElementById("pass_num").focus();
        return false;
      } else if (!passnumtest.test(pass_num)) {
        alert(
          "  1  2 +    . "
        );
        document.getElementById("pass_num").focus();
        return false;
      } else if (PHONE == "") {
        alert("  ");
        document.getElementById("PHONE").focus();
        return false;
      } else if (!phoneTest.test(PHONE)) {
        alert(
          "   \n -   ()"
        );
        document.getElementById("PHONE").focus();
        return false;
      } else if (POST == "" || ADDR1 == "" || ADDR2 == "") {
        alert(" ");
        document.getElementById("POST").focus();
        return false;
      } else if (!postTest.test(POST)) {
        alert(" ");
        document.getElementById("POST").focus();
        return false;
      }

      const params = new URLSearchParams();
      params.append("pass_num", pass_num);
      params.append("phone", PHONE);
      params.append("post", POST);
      params.append("addr1", ADDR1);
      params.append("addr2", ADDR2);
      params.append("email", this.$session.get("email"));

      this.$axios.post("/user/updateinfo", params).then(res => {
        if (res.data == "success") {
          alert("  ");
          window.location.href = "/";
        } else {
          alert("   \n  ");
        }
      });
    }
  }
};
</script>

<style scoped>
.btn-primary {
  width: 30%;
}
.v-application .mt-3 {
  margin-top: 30px !important;
}
.row + .row {
  margin-top: 0px;
}
.extra {
  margin-bottom: 20px;
}
.extra2 {
  padding-left: 12px;
  padding-right: 205px;
}
</style>



