<!-- eslint-disable -->

<template>
  <v-app id="app">
    <v-main style="margin:0px auto">
      <h1 style="text-align:center;margin-top:20%">  </h1>
      <br />
      <v-container style="position: relative;" class="text-xs-center">
        <v-layout row wrap class="text-xs-center">
          <v-flex>
            <v-card flat class="mx-auto" max-width="800">
              <v-row>
                <v-col>
                  <v-form
                    @submit.prevent="deleteuser()"
                    style="height: 300px; margin-top:20%; text-align:center;"
                  >
                    <div class="mx-3">
                      <div class="mx-1">
                        <p>    </p>
                        <p>   </p>
                        <br />
                        <p style="color:red;">
                                 
                          
                        </p>
                        <br />
                      </div>
                    </div>
                    <div class="mx-3">
                      <div class="mx-1">
                        <div id="checkbox">
                          <input
                            type="checkbox"
                            value="checked"
                            v-model="checked"
                          />
                          <label
                            >   , 
                            </label
                          >
                        </div>
                      </div>
                    </div>
                    <v-card-actions>
                      <v-btn
                        type="submit"
                        class="form-control"
                        outlined
                        color="info"
                        style="background: cornflowerblue;"
                      >
                        <span class="login_content"> </span>
                      </v-btn>
                    </v-card-actions>
                  </v-form>
                </v-col>
              </v-row>
            </v-card>
          </v-flex>
        </v-layout>
      </v-container>
    </v-main>
  </v-app>
</template>

<script>
/* eslint-disable */

export default {
  data() {},
  methods: {
    deleteuser() {
      if (this.checked) {
        const params = new URLSearchParams();
        params.append("email", this.$session.get("email"));
        this.$axios.post("/user/deletereservation", params).then(res => {
          if (res.data == "success") {
            alert("      .");
            return false;
          } else {
            this.$axios.post("/user/deleteuser", params).then(res => {
              if (res.data.includes("success")) {
                this.$session.destroy();
                alert("  ");
                window.location.href = "/";
              } else {
                alert("  \n  .");
              }
            });
          }
        });
      } else {
        alert(" ");
      }
    }
  }
};
</script>
<style scoped>
#checkbox input {
  display: inline-block;
  margin-right: 10px;
}
#checkbox label {
  display: inline-block;
}
</style>



