<!--eslint-disable-->
<template>
  <v-form>
    <v-container>
      <form @submit.prevent="onSubmit">
        <div>
          <h1 style="padding-top: 50px;"></h1>
          <hr />
          <b-input-group prepend=" " class="mt-10">
            <b-form-select
              v-model="selected"
              :options="options"
              class="form-control"
              required
              id="category"
              name="category"
            ></b-form-select>
          </b-input-group>
          <b-input-group prepend=" " class="mt-10">
            <b-form-input
              required
              id="title"
              name="title"
              v-model="title"
              placeholder=" 15  ( )"
            ></b-form-input>
          </b-input-group>
          <b-input-group prepend=" " class="mt-10">
            <span style="position:absolute; right:0px; top:10px;">
              <span :style="[text.length > 100 ? { color: 'red' } : {}]">{{
                text.length
              }}</span>
              / 100
            </span>
          </b-input-group>
          <b-form-textarea
            id="content"
            name="content"
            placeholder=" 100  ( )"
            rows="5"
            max-rows="5"
            required
            v-model="text"
            :state="text.length < 101"
            no-resize
          ></b-form-textarea>
        </div>
        <br />
        <div style="padding-bottom: 50px;">
          <v-btn
            type="submit"
            outlined
            color="info"
            style="float: right; background: blue;"
          >
            <span class="plane-search_content" style="color: white;"
              ></span
            >
            <p></p>
          </v-btn>
        </div>
      </form>
    </v-container>
  </v-form>
</template>

<script>
/* eslint-disable */
export default {
  data() {
    return {
      selected: null,
      options: [
        { value: null, text: "  " },
        { value: "   ", text: "   " },
        { value: " ", text: " " },
        { value: " ", text: " " },
        { value: "", text: "" }
      ],
      text: "",
      category: "",
      title: "",
      content: ""
    };
  },
  methods: {
    onSubmit() {
      //  -   1  15 
      var titletest = /^.{1,15}$/;
      //  -   1  100 
      var contenttest = /^.{1,100}$/;

      this.category = document.getElementById("category").value;
      this.title = document.getElementById("title").value;
      this.content = document.getElementById("content").value;

      if (!titletest.test(this.title)) {
        alert("  15  ");
        this.title = this.title.substring(0,15);
        document.getElementById("title").focus();
        return false;
      } else if (!contenttest.test(this.content)) {
        alert("  100  ");
        this.text = this.text.substring(0,100);
        document.getElementById("content").focus();
        return false;
      }

      const params = new URLSearchParams();
      params.append("email", this.$session.get("email"));
      params.append("category", this.category);
      params.append("title", this.title);
      params.append("content", this.content);

      this.$axios.post("/user/insertqna", params).then(res => {
        if (res.data == "success") {
          alert(" ");
          window.location.href = "/myqna";
        } else {
          alert(" \n  ");
          window.location.href = "/myqna";
        }
      });
    }
  }
};
</script>



