<!--eslint-disable-->
<template>
    <v-form>
        <v-container>
            <form @submit.prevent="onSubmit">
                <div class="container col-sm-6">
                    <h3 style="font-weight:bold"></h3>
                    <br />
                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-envelope icon"></i>
                            <label for="email">E-mail(ID)<span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <div class="row">
                                <div class="col-sm-12">
                                    <input type="text" class="form-control" placeholder="E-mail" id="email" name="email" maxlength="90" v-model="email" @blur="emailFormatter(email)" @focus="availableEmail = false"/>
                                    <span class="badge badge-danger mt-1" v-show="availableEmail" id="emailCheck">{{emailMsg}}</span>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-user icon"></i>
                            <label for="username"><span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" placeholder="" id="username" name="username" maxlength="20" />
                        </div>
                    </div>

                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-user icon"></i>
                            <label for="surname">()<span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" placeholder=")HONG" id="surname" name="surname" maxlength="15" />
                        </div>
                    </div>

                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-user icon"></i>
                            <label for="name">()<span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" placeholder=")GILDONG" id="name" name="name" maxlength="30" />
                        </div>
                    </div>

                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-user icon"></i>
                            <label for="birthday"><span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" id="birthday" v-model="birthday" placeholder=" 8 (1900-00-00)" maxlength="8" @blur="birthdayFormatter(birthday)" @focus="birthday = ''" />
                        </div>
                    </div>

                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-plane icon"></i>
                            <label for="pass_num"><span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" placeholder="" id="pass_num" name="pass_num" maxlength="9" />
                        </div>
                    </div>

                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-phone icon"></i>
                            <label for="phone"><span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <input type="text" class="form-control" placeholder="-   " id="phone" name="phone" maxlength="11" />
                        </div>
                    </div>

                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-key icon"></i>
                            <label for="password"><span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <input type="password" class="form-control" placeholder="" id="password" name="password" maxlength="20" />
                        </div>
                    </div>
                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-key icon"></i>
                            <label for="passwordcheck"> <span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-8">
                            <input type="password" class="form-control" placeholder=" " id="passwordcheck" name="passwordcheck" maxlength="20" />
                        </div>
                    </div>

                    <div class="row col-sm-12">
                        <div class="col-sm-4">
                            <i class="fa fa-book icon"></i>
                            <label for="addr"><span class="text-danger">*</span></label>
                        </div>
                        <div class="col-sm-7">
                            <div class="row">
                                <div class="col-sm-7">
                                    <input type="text" class="form-control" placeholder=" " id="post" name="post" maxlength="5" />
                                </div>
                                <div class="col-sm-5">
                                    <button id="addr" class="btn btn-warning form-control" @click.prevent="postSearch">
                                         
                                    </button>
                                </div>
                            </div>
                            <div class="row">
                                <div class="col-sm-12">
                                    <input type="text" placeholder="" id="addr1" name="addr1" class="form-control" maxlength="40" />
                                </div>
                                <div class="col-sm-12">
                                    <input type="text" placeholder="" id="addr2" name="addr2" class="form-control" maxlength="40" />
                                </div>
                            </div>
                        </div>
                    </div>
                    <br />
                    <button type="submit" class="btn btn-info form-control">
                        
                    </button>
                </div>
            </form>
        </v-container>
    </v-form>
</template>

<script>
/* eslint-disable */

export default {
  data() {
    return {
      availableEmail: false,
      availableEmail2: false,
      username: "",
      surname: "",
      name: "",
      pass_num: "",
      birthday: "",
      email: "",
      password: "",
      passwordcheck: "",
      post: "",
      addr1: "",
      addr2: "",
      phone: "",
      emailMsg: "",
    };
  },
  methods: {
    birthdayFormatter(birthday) {
      if (birthday.length == 8) {
        this.birthday = birthday.replace(/(\d{4})(\d{2})(\d{2})/, "$1-$2-$3");
      }
    },
    emailFormatter(email) {
      // () 
      var idTest = /^[\w!#$%&'*+/=?^_{|}~-]+(?:\.[\w!#$%&'*+/=?^_{|}~-]+)*@(?:\w+\.)+\w+$/g;
      document.getElementById("emailCheck").className = "badge badge-danger mt-1";
      this.availableEmail2 = false;

      if(email == ""){
        this.emailMsg = ' ';
        this.availableEmail = true;
      } else if(!idTest.test(email)) {
        this.emailMsg = '  \nex) email@naver.com';
        this.availableEmail = true;
        return false;
      } else if (email.length > 91) {
        this.emailMsg = '  90  ';
        this.availableEmail = true;
        return false;
      }
      const params = new URLSearchParams();
      params.append("email", email);

      this.$axios.post("/user/idChecked", params).then(res => {
        if (res.data == false) {
          this.emailMsg = '  .';
          this.availableEmail = true;
        } else {
          this.emailMsg = ' .';
          document.getElementById("emailCheck").className = "badge badge-success mt-1";
          this.availableEmail = true;
          this.availableEmail2 = true;
          
        }
      });
    },
    postSearch() {
      new window.daum.Postcode({
        oncomplete: data => {
          if (data.jibunAddress == "") {
            document.getElementById("addr1").value = data.roadAddress;
          } else {
            document.getElementById("addr1").value = data.jibunAddress;
          }
          document.getElementById("post").value = data.zonecode;
          document.getElementById("addr2").focus();
        }
      }).open();
    },
    onSubmit() {
      this.username = document.getElementById("username").value;
      this.email = document.getElementById("email").value;
      this.surname = document.getElementById("surname").value;
      this.name = document.getElementById("name").value;
      this.pass_num = document.getElementById("pass_num").value;
      this.birthday = document.getElementById("birthday").value;
      this.password = document.getElementById("password").value;
      this.passwordcheck = document.getElementById("passwordcheck").value;
      this.post = document.getElementById("post").value;
      this.addr1 = document.getElementById("addr1").value;
      this.addr2 = document.getElementById("addr2").value;
      this.phone = document.getElementById("phone").value;

      //     
      var nameTest = /^[-]{2,20}$/g;
      //   
      var nametest = /^[A-Z]+$/g;
      //   
      var surnametest = /^[A-Z]{1,15}$/g;
      //  
      var birthdaytest = /^(19[0-9][0-9]|20\d{2})-(0[0-9]|1[0-2])-(0[1-9]|[1-2][0-9]|3[0-1])$/g;
      //   
      var passnumtest = /^(.*)[a-zA-Z]{1}[0-9a-zA-Z]{1}[0-9]{7}(.*)$/g;
      //  
      var pwTest = /^(?=.*[A-Za-z])(?=.*\d)(?=.*[$@$!%*#?&])[A-Za-z\d$@$!%*#?&]{8,20}$/g;
      //  
      var postTest = /^[0-9]{5}$/g;
      //  
      var phoneTest = /^01[0-9]{9}$/g;
      //  
      var addrTest = /^.{1,40}$/;

      if (this.email == "") {
        alert(" ");
        document.getElementById("email").focus();
        return false;
      } else if(!this.availableEmail2){
        alert(" ");
        return false;
      } else if (this.username == "") {
        alert(" ");
        document.getElementById("username").focus();
        return false;
      } else if (!nameTest.test(this.username)) {
        alert(" !()\n  \n 2,  20");
        document.getElementById("username").focus();
        return false;
      } else if (this.surname == "") {
        alert(" ");
        document.getElementById("surname").focus();
        return false;
      } else if (!surnametest.test(this.surname)) {
        alert("   . ");
        document.getElementById("surname").focus();
        return false;
      } else if (this.name == "") {
        alert(" ");
        document.getElementById("name").focus();
        return false;
      } else if (!nametest.test(this.name)) {
        alert("   . ");
        document.getElementById("name").focus();
        return false;
      } else if (this.birthday == "") {
        alert(" ");
        return false;
      } else if (!birthdaytest.test(this.birthday)) {
        alert("  . \n  . ");
        document.getElementById("birthday").focus();
        return false;
      } else if (this.pass_num == "") {
        alert(" ");
        document.getElementById("pass_num").focus();
        return false;
      } else if (!passnumtest.test(this.pass_num)) {
        alert("  1  2 +    . ");
        document.getElementById("pass_num").focus();
        return false;
      } else if (this.phone == "") {
        alert("  ");
        document.getElementById("phone").focus();
        return false;
      } else if (!phoneTest.test(this.phone)) {
        alert("   \n -   ()");
        document.getElementById("phone").focus();
        return false;
      } else if (this.password == "") {
        alert(" ");
        document.getElementById("password").focus();
        return false;
      } else if (!pwTest.test(this.password)) {
        alert(" !()\n 8 ,  20 ,   ,      ");
        document.getElementById("password").focus();
        return false;
      } else if (this.passwordcheck == "") {
        alert("  ");
        document.getElementById("passwordcheck").focus();
        return false;
      } else if (this.password != this.passwordcheck) {
        alert("  ");
        document.getElementById("passwordcheck").focus();
        return false;
      } else if (this.post == "") {
        alert(" ");
        document.getElementById("post").focus();
        return false;
      } else if (!postTest.test(this.post)) {
        alert(" ");
        document.getElementById("post").focus();
        return false;
      } else if (this.addr1 == "") {
        alert(" ");
        document.getElementById("addr1").focus();
        return false;
      } else if (!addrTest.test(this.addr1)) {
        alert("    40 ");
        document.getElementById("addr1").focus();
        return false;
      } else if (this.addr2 == "") {
        alert("  ");
        document.getElementById("addr2").focus();
        return false;
      } else if (!addrTest.test(this.addr2)) {
        alert("     40 ");
        document.getElementById("addr2").focus();
        return false;
      }
      const params = new URLSearchParams();
      params.append("user_name", this.username);
      params.append("surname", this.surname);
      params.append("name", this.name);
      params.append("pass_num", this.pass_num);
      params.append("birthday", this.birthday);
      params.append("email", this.email);
      params.append("password", this.password);
      params.append("post", this.post);
      params.append("addr1", this.addr1);
      params.append("addr2", this.addr2);
      params.append("phone", this.phone);

      this.$axios.post("/user/join", params).then(res => {
        if (res.data == "success") {
          alert(" ");
        } else {
          alert("  \n ");
        }
        window.location.href = "/login";
      });
    }
  }
};
</script>

<style scoped lang="css">
.input-container {
  display: -ms-flexbox; /* IE10 */
  display: flex;
  width: 100%;
  margin-bottom: 15px;
}

.icon {
  padding: 10px;
  background: skyblue;
  color: white;
  min-width: 50px;
  text-align: center;
}

.input-field {
  width: 100%;
  padding: 10px;
  outline: none;
}

.input-field:focus {
  border: 2px solid skyblue;
}

/* Set a style for the submit button */
.btn {
  background-color: dodgerblue;
  color: white;
  padding: 10px 10px;
  border: none;
  cursor: pointer;
  width: 100%;
  opacity: 0.9;
}

.btn:hover {
  opacity: 1;
}
</style>



